/* eslint-disable no-mixed-spaces-and-tabs */
import React, { useState } from "react"
import { Row, Col, Card, Form, Comment, Tooltip, Icon, Divider, message, List } from "antd"
import styled from "styled-components"
import { Formik } from "formik"
import { SubmitButton, ResetButton } from "formik-antd"
import moment from "moment"
import { useMutation } from "react-apollo"
import FadeIn from "react-fade-in"

import { MUTATE_ADD_REPLY_FEEDS, MUTATE_FEED_LIST, MUTATE_ADD_FEED, MUTATE_VOTE_FEED } from "queries/user"
import { Avatar, Heading, Empty } from "components"
import TextInput from "components/forms/TextInput"
import { serverBaseUrl, newClient, media, PLACEHOLDER_OWL_IMAGE } from "helpers"
import { baseStyles } from "styles/base"
import ModalReply from "./ModalReply"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import useAvatar from "helpers/hooks/useAvatar"
import { useEffect } from "react"
import { Layout } from "components"
import Section from "components/Section"
import ResultSection from "components/ResultSection"
import Button from "components/Button"
import { useHistory } from "react-router"
import { country } from "App"
import { MUTATE_USER_PROFILE_HOME } from "queries/profile"

const NewFeedFCard = styled(Card).attrs({
	autoHeight: true
})`
	&& {
		padding-left: 4em;
		padding-right: 4em;
		margin-bottom: 2em;
		background-color: transparent;
		border-color: transparent;
		.ant-card-body {
			padding: 0;
		}
	}

	${media.mobile`
		&& {
			padding-left: 0;
			padding-right: 0;
		}
	`}
`

const ActionItem = styled.span`
	margin-right: 1.4em;
`

const StyledList = styled(List)`
	&& {
		padding-left: 4em;
		padding-right: 4em;
		max-height: 100vh;
		overflow-y: scroll;
	}

	${media.mobile`
		&& {
			padding-left: 0;
			padding-right: 0;
		}
	`}
`

const StyledComment = styled(Comment)`
	padding: 2em;
	background-color: #fff;
	border-radius: 10px;
	margin-bottom: 1.5em;
	box-shadow: ${baseStyles.boxShadow.main};
	transition: all 0.2s ease;
	.ant-comment-inner {
		padding: 0;
		margin-bottom: ${({ comments }) => comments.length > 0 && "2em"};
		.ant-comment-content-author-time {
			color: ${baseStyles.greyColor};
		}
		+ .ant-comment-nested {
			> div:last-child {
				.ant-row {
					margin-bottom: 0;
				}
			}
		}
	}
	.ant-row {
		margin-bottom: 1em;
	}
	&:hover {
		box-shadow: ${baseStyles.boxShadow.hover};
	}
`

const ReplyItem = styled(Col)`
	> p {
		font-size: 12px;
	}
`

const feedClient = newClient("feeds")
const DEFAULT_FEED_LENGTH = 15

function Feeds({ data = {}, i18n }) {
	const [action, setAction] = useState("")
	const [selectedItem, setSelectedItem] = useState({})
	const [userFeed, setUserFeed] = useState([])
	const [userProfile, setUserProfile] = useState({})
	const { push } = useHistory()

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}

	const avatar = (userProfile.avatar || [])[0] || {}

	const avaFromHooks = useAvatar(avatar.path)
	const actualUploadedAva = userProfile.avatar && serverBaseUrl + (userProfile.avatar[userProfile.avatar.length - 1] || {}).path
	const userAvatar = (userProfile.avatar || []).length > 1 ? actualUploadedAva : avaFromHooks

	const [fetchUserProfile] = useMutation(MUTATE_USER_PROFILE_HOME, {
		client: newClient(),
		onCompleted: (data) => {
			data = data.getProfile || {}
			setUserProfile(data)
		}
	})

	const [fetchFeedList] = useMutation(MUTATE_FEED_LIST, {
		client: feedClient,
		onCompleted: (data) => {
			data = (data.getFeeds || {}).list
			setUserFeed(data)
		}
	})

	const [addCommentOnFeeds] = useMutation(MUTATE_ADD_FEED, {
		client: feedClient,
		onCompleted: () => {
			fetchFeedList({ variables: { startIndex: 0, length: 10 } }).then(() => {
				message.success(i18n._(t`Your comment has been added`))
			})
		}
	})

	const [addVoteOnFeeds] = useMutation(MUTATE_VOTE_FEED, {
		client: feedClient,
		onCompleted: ({ addVote = {} }) => {
			const { vote } = addVote
			fetchFeedList({ variables: { startIndex: 0, length: DEFAULT_FEED_LENGTH } }).then(() => {
				message.success(i18n._(t`You've ${vote === 1 ? "liked" : "disliked"} this comment`))
			})
		}
	})

	const [addReply] = useMutation(MUTATE_ADD_REPLY_FEEDS, {
		client: feedClient,
		onCompleted: () => {
			setSelectedItem({})
			fetchFeedList({ variables: { startIndex: 0, length: DEFAULT_FEED_LENGTH } }).then(() => {
				message.success(i18n._(t`Reply has been sent`))
			})
		}
	})

	const handleSubmitFeed = (values, { setSubmitting, resetForm }) => {
		addCommentOnFeeds({ variables: { ...values } }).finally(() => {
			resetForm({})
			setSubmitting(false)
		})
	}

	const handleReactions = (pvid, type) => {
		const value = type === "likes" ? 1 : -1
		addVoteOnFeeds({ variables: { feedCommentPvid: pvid, vote: value } })
	}

	const handleShowCommentForm = (item) => setSelectedItem(item)

	useEffect(() => {
		if (localStorage.getItem("token")) {
			fetchFeedList({ variables: { startIndex: 0, length: DEFAULT_FEED_LENGTH } })
			fetchUserProfile()
		}
	}, [fetchFeedList, fetchUserProfile])

	if (!localStorage.getItem("token")) {
		return (
			<Layout>
				<Section>
					<Row gutter={32} type="flex" justify="center">
						<Col lg={14} xs={24}>
							<Heading content={<Trans>Feeds</Trans>} level={4} marginBottom="3em" />
							<ResultSection
								title={
									<Heading
										level={4}
										content={<Trans>Oops! You are not logged in</Trans>}
										subheader={
											<>
												<br />
												<Button type="primary" onClick={() => push(`/${country}/login`)}>
													<Trans>Login first</Trans>
												</Button>
											</>
										}
									/>
								}
							/>
						</Col>
					</Row>
				</Section>
			</Layout>
		)
	}

	return (
		<Layout>
			<Section>
				<Row gutter={32} type="flex" justify="center">
					<ModalReply
						data={selectedItem}
						avatar={userAvatar}
						handlers={{ addReply, setSelectedItem }}
						visible={Object.keys(selectedItem).length > 0}
					/>
					<Col lg={14} xs={24}>
						<Heading content={<Trans>Feeds</Trans>} level={4} marginBottom="3em" />
						<NewFeedFCard>
							<Row gutter={16}>
								<Col lg={4} xs={4}>
									<Avatar src={userAvatar} size="large" />
								</Col>
								<Col lg={20} xs={20}>
									<Formik onSubmit={handleSubmitFeed} initialValues={{ comment: "" }}>
										{({ handleSubmit, dirty }) => (
											<Form layout="vertical" onSubmit={handleSubmit}>
												<TextInput textarea rows={4} name="comment" placeholder={i18n._(t`Any thought on mind?`)} />
												<SubmitButton disabled={!dirty} type="primary" size="large" icon="plus">
													&nbsp; <Trans>Send feed</Trans>
												</SubmitButton>{" "}
												&nbsp;{" "}
												<ResetButton size="large" type="link">
													<Trans>Reset</Trans>
												</ResetButton>
											</Form>
										)}
									</Formik>
								</Col>
							</Row>
						</NewFeedFCard>

						<Divider />

						<Heading content={<Trans>Your feeds</Trans>} level={4} className="px4em px0-mobile" />

						<StyledList
							className="comment-list"
							header={<Trans>{userFeed.length} entries</Trans>}
							itemLayout="horizontal"
							dataSource={userFeed}
							locale={{ emptyText: <Empty boxImage description={<Trans>No feed entries found</Trans>} /> }}
							renderItem={(item) => {
								const feedAuthor = item.firstName ? item.firstName + " " + item.lastName : "No name"
								const avatar = item.avatar ? serverBaseUrl + item.avatar : serverBaseUrl + PLACEHOLDER_OWL_IMAGE

								return (
									<FadeIn key={item.pvid}>
										<li>
											<StyledComment
												actions={[
													<ActionItem key="comment-basic-like">
														<Tooltip title={<Trans>Like</Trans>}>
															<Icon
																type="like"
																theme={action === "liked" ? "filled" : "outlined"}
																onClick={() => handleReactions(item.pvid, "likes")}
															/>
														</Tooltip>
														<span style={{ paddingLeft: 5, cursor: "auto" }}>{item.totalUpVoted}</span>
													</ActionItem>,
													<ActionItem key="comment-basic-dislike">
														<Tooltip title={<Trans>Dislike</Trans>}>
															<Icon
																type="dislike"
																theme={action === "disliked" ? "filled" : "outlined"}
																onClick={() => handleReactions(item.pvid, "dislikes")}
															/>
														</Tooltip>
														<span style={{ paddingLeft: 5, cursor: "auto" }}>{item.totalDownVoted}</span>
													</ActionItem>,
													<ActionItem key="comment-basic-reply-to" onClick={() => handleShowCommentForm(item)}>
														<Trans>Reply to</Trans>
													</ActionItem>
												]}
												comments={item.list}
												author={<a>{feedAuthor}</a>}
												avatar={<Avatar src={avatar} alt={`${item.firstName} ${item.lastName}`} size="large" />}
												content={<p>{item.comment}</p>}
												datetime={
													<Tooltip title={moment(item.postedDate).format("dddd, DD MMM YYYY HH:mm:ss")}>
														<span>{moment(item.postedDate).fromNow()}</span>
													</Tooltip>
												}
											>
												{(item.list || []).length > 0
													? item.list.map((item) => (
															<FadeIn key={item.pvid}>
																<Row gutter={32}>
																	<Col lg={4} xs={4}>
																		<Avatar src={avatar} size="large" />
																	</Col>
																	<ReplyItem lg={20} xs={20}>
																		<p>
																			{item.firstName} {item.lastName} &nbsp;
																			<span className="grey">
																				{moment(item.postedDate).fromNow()}
																			</span>
																		</p>
																		<p>{item.comment}</p>
																	</ReplyItem>
																</Row>
															</FadeIn>
													  ))
													: null}
											</StyledComment>
										</li>
									</FadeIn>
								)
							}}
						/>
						{/* {userFeed.length > 10 && DEFAULT_FEED_LENGTH > 10 && (
							<ButtonLink icon="down" block>
								&nbsp; <Trans>Load more...</Trans>
							</ButtonLink>
						)} */}
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

export default withI18n()(Feeds)
