import React from "react"
import { Row, Col, Form } from "antd"
import { Formik } from "formik"
import { SubmitButton } from "formik-antd"
import styled from "styled-components"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

import TextInput from "components/forms/TextInput"
import { Modal, Avatar, Heading } from "components"
import { baseStyles } from "styles/base"
import { mobile, media, serverBaseUrl } from "helpers"
import { useEffect } from "react"

const StyledModal = styled(Modal)`
	.ant-modal-content {
		padding: 2em 1em;
		.ant-avatar {
			width: 80px;
			height: 80px;
			line-height: 80px;
			font-size: 20px;
		}
		.ant-row:first-child {
			margin-bottom: 3em;
		}
		h4.ant-typography {
			font-size: 1em;
			margin-bottom: 1em;
			.datetime {
				color: ${baseStyles.greyColor};
			}
		}
	}

	${media.mobile`
		.ant-modal-content {
			.ant-avatar {
				width: 60px;
				height: 60px;
				line-height: 60px;
			}
		}
	`}
`

function ModalReply({ data = {}, avatar, handlers, i18n, ...props }, ref) {
	const { firstName = "", lastName = "", comment = "", postedDate = "" } = data
	const { addReply, setSelectedItem } = handlers
	const avatarAlias = firstName && lastName ? `${firstName[0]}${lastName[0]}` : firstName.slice(0, 2).toUpperCase()
	const heading = (
		<span>
			{firstName} {lastName} &middot; <span className="datetime">{moment(postedDate).fromNow()}</span>
		</span>
	)
	const inputRef = React.createRef(null)

	const handleSubmitReply = (values, { setSubmitting, resetForm }) => {
		values = { ...values, feedCommentPvid: data.pvid }
		addReply({ variables: { ...values } }).then(() => {
			setSubmitting(false)
			resetForm({})
		})
	}

	const handleCloseModal = () => {
		setSelectedItem({})
	}

	useEffect(() => {
		inputRef.current && inputRef.current.focus()
	}, [inputRef])

	return (
		<StyledModal {...props} onCancel={handleCloseModal} centered footer={false} width={650}>
			<Row gutter={32}>
				<Col lg={5} xs={6}>
					<Avatar src={serverBaseUrl + data.avatar} size={mobile ? "default" : "large"}>
						{avatarAlias}
					</Avatar>
				</Col>
				<Col lg={19} xs={18}>
					<Heading content={heading} subheader={comment} level={4} />
				</Col>
			</Row>
			<Row gutter={32}>
				<Col lg={5} xs={6}>
					<Avatar src={avatar} size="default">
						{avatarAlias}
					</Avatar>
				</Col>
				<Col lg={19} xs={18}>
					<Formik onSubmit={handleSubmitReply} initialValues={{ comment: "" }}>
						{({ handleSubmit, values, setValues }) => (
							<Form
								layout="vertical"
								onSubmit={handleSubmit}
								onKeyDown={e => {
									if (e.metaKey && e.key === "Enter") handleSubmit()
								}}
							>
								<p className="grey">
									<Trans>
										Replying to{" "}
										<strong>
											{firstName} {lastName}
										</strong>
									</Trans>
								</p>
								<TextInput
									// autoFocus
									transparent
									ref={ref}
									textarea
									rows={2}
									name="comment"
									placeholder={i18n._(t`Write your reply...`)}
									helpText={values.comment && <Trans>Cmd + Enter (or Ctrl + Enter in Windows) to submit</Trans>}
								/>
								<SubmitButton type="primary" icon="plus">
									&nbsp; <Trans>Reply</Trans>
								</SubmitButton>
							</Form>
						)}
					</Formik>
				</Col>
			</Row>
		</StyledModal>
	)
}

export default withI18n()(React.forwardRef(ModalReply))
